import "./App.css";
import Home from "./views/Home";
import { Route, Switch } from "react-router-dom";
import NavBar from "./components/NavBar";
import ChristmasList from "./views/ChristmasList";
import { HashRouter } from "react-router-dom/cjs/react-router-dom";

function App() {
  return (
    <HashRouter basename="/">
      <div className="App">
        <NavBar />
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/phoebe/list">
            <ChristmasList />
          </Route>
        </Switch>
      </div>
    </HashRouter>
  );
}

export default App;
