import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import { Button } from "@mui/material";

export default function NavBar() {
  return (
    <Box sx={{ display: "flex" }}>
      <AppBar position="static" style={{ background: "#294A2B" }}>
        <Toolbar>
          <Typography
            variant="h6"
            component="div"
            align="left"
            sx={{ flexGrow: 1 }}
          >
            <Button
              variant="text"
              color="inherit"
              href="/"
              sx={{ textTransform: "capitalize" }}
            >
              Phoebe Hartin
            </Button>
          </Typography>
          <Box>
            <Button variant="text" color="inherit" href="#/phoebe/list">
              Christmas List
            </Button>
          </Box>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
