import {
  ListItem,
  IconButton,
  ListItemIcon,
  ListItemText,
  Checkbox,
  Tooltip,
  checkboxClasses,
} from "@mui/material";
import LaunchIcon from "@mui/icons-material/Launch";
import { useState } from "react";

export default function ChristmasListItem({ itemName, itemLink }) {
  const [isCrossedOut, setIsCrossedOut] = useState(false);
  return (
    <ListItem
      key={itemName}
      secondaryAction={
        <Tooltip title="Open link">
          <IconButton
            edge="end"
            aria-label="openLink"
            href={itemLink}
            target="_blank"
          >
            <LaunchIcon sx={{ color: "#463220" }} />
          </IconButton>
        </Tooltip>
      }
    >
      <ListItemIcon>
        <Checkbox
          sx={{
            [`&, &.${checkboxClasses.checked}`]: {
              color: "#463220",
            },
          }}
          edge="start"
          tabIndex={-1}
          disableRipple
          onClick={() => {
            if (isCrossedOut) setIsCrossedOut(false);
            else setIsCrossedOut(true);
          }}
        />
      </ListItemIcon>
      <ListItemText
        id={itemName}
        primary={itemName}
        sx={isCrossedOut && { textDecoration: "line-through" }}
      />
    </ListItem>
  );
}
